@import "style/mixins.module.scss";

.root {
    display: flex;
    padding-bottom: 24px;

    @include respond-to("laptop") {
        padding-bottom: 16px;
    }

    @include respond-to("laptop-L") {
        padding-bottom: 24px;
    }

    &:first-child {
        border-bottom: 1px solid rgba(66, 78, 88, 0.1);
    }

    &:last-child {
        padding-top: 24px;
        padding-bottom: 0;

        @include respond-to("laptop") {
            padding-top: 16px;
        }

        @include respond-to("laptop-L") {
            padding-top: 24px;
        }
    }
}

.name {
    width: 96px;
    margin-bottom: 0;
    margin-right: 16px;

    @include respond-to("laptop") {
        width: 148px;
    }

    @include respond-to("laptop-L") {
        margin-right: 24px;
    }
}

.price {
    margin-bottom: 0;
}

.period {
    display: flex;
    height: inherit;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-left: 16px;
}

.month,
.periods,
.prepayment {
    margin-bottom: 0;

    font-size: 13px;
    line-height: 20px;

    @include respond-to("laptop-L") {
        font-size: 17px;
        line-height: 24px;
    }
}

.amount {
    display: flex;
}

.value {
    margin-bottom: 0;
    padding: 0;
    border: 0;
}
