@import "style/mixins.module.scss";

.root {
    width: 100%;
}

.head {
    padding-bottom: 0;
    margin-bottom: 80px;

    @include respond-to("laptop") {
        padding-bottom: 202px;
    }
}

.appointment {
    margin-bottom: 80px;
}

.price {
    margin-bottom: 64px;

    @include respond-to("laptop-L") {
        margin-bottom: 80px;
    }
}

.results {
    margin-bottom: 55px;

    @include respond-to("laptop") {
        margin-bottom: 80px;
    }

    @include respond-to("laptop-L") {
        margin-bottom: 160px;
    }
}

.table {
    margin-bottom: 30px;

    @include respond-to("laptop") {
        margin-bottom: 80px;
    }

    @include respond-to("laptop-L") {
        margin-bottom: 160px;
    }
}

.appointment-title {
    @include formTitle();
}