.header {
    margin-bottom: 24px;
}

.description {
    margin-bottom: 0;
    font-weight: 400;
}

.title {
    margin-bottom: 8px;
}

.point {
    width: 100%;
    margin-bottom: 40px;
}

.poster_with-fix-height {
    height: 240px;
}

.row {
    display: flex;
    flex-direction: column-reverse;
}

.column {
    &_content {
        width: 100%;
    }

    &_posters {
        display: none;
    }

    &_posters_one_img {
        display: block;
    }
}

.one_poster {
    width: 100%;
    object-fit: cover;
}

.column_posters_one_img {
    margin-bottom: 24px;
}

@media (min-width: 1024px) {
    .column_posters_one_img {
        margin-bottom: 0;
    }
    .title {
        margin-bottom: 24px;
    }

    .root {
        display: flex;
    }

    .one_poster {
        height: 504px;
    }

    .row {
        flex-direction: row;
    }

    .header {
        margin-bottom: 56px;
    }

    .content {
        padding: 40px 70px 0 0;
    }

    .column {
        &_content {
            width: 60%;
        }

        &_content_one_img {
            width: 50%;
        }

        &_posters {
            display: block;
            width: 40%;

            img {
                max-width: 100%;
            }
        }

        &_posters_one_img {
            width: 50%;
        }
    }

    .list {
        padding-bottom: 36px;
    }

    .list_without_pb {
        padding-bottom: 0;
    }

    .point {
        width: 50%;
        margin-bottom: 56px;

        padding-right: 20px;

        &:nth-child(even) {
            padding-right: 0;
            padding-left: 20px;
        }
    }
}

@media (min-width: 1440px) {
    .one_poster {
        height: 696px;
    }

    .description {
        font-size: 22px;
        line-height: 32px;
    }
}
