@import "style/mixins.module.scss";

.root {
    position: relative;

    @include respond-to("laptop") {
        background-image: url("/special/images/new/head/bg-head.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        padding-bottom: 430px;
    }
}

.top-content {
    position: relative;

    padding-bottom: 388px;

    background-image: url("/special/images/new/head/bg-head-mob.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    @include respond-to("laptop") {
        background-image: none;
        padding-bottom: 0;
    }
}

.top-content-container {
    margin-bottom: 56px;

    @include respond-to("laptop") {
        margin-bottom: 0;
    }
}

.column {
    position: relative;
    padding-top: 40px;
    max-width: 386px;
    z-index: 1;

    @include respond-to("laptop-L") {
        max-width: 580px;
        padding-top: 130px;
    }
}

.title {
    font-size: 36px;
    line-height: 40px;
    font-weight: 700;
    font-family: Inter, sans-serif;

    @include respond-to("laptop-L") {
        font-size: 56px;
        line-height: 64px;
    }
}

.subtitle {
    display: none;

    @include respond-to("laptop") {
        display: inline-block;
    }
}