@import "style/mixins.module.scss";

.column-content {
    padding: 0;

    @include respond-to("laptop") {
        width: 50%;
        padding-left: 40px;
    }

    @include respond-to("laptop-L") {
        padding-left: 64px;
    }
}

.prices {
    padding-bottom: 24px;

    @include respond-to("laptop-L") {
        padding-bottom: 40px;
    }
}

.header {
    margin-bottom: 24px;
}

.button-container {
    @include respond-to("laptop-L") {
        display: flex;
        align-items: center;
        padding-bottom: 24px;
        border-bottom: 1px solid rgba(66, 78, 88, 0.1);
    }
}

.button {
    margin-bottom: 16px;

    @include respond-to("laptop") {
        margin: 0 24px 16px 0;
    }

    @include respond-to("laptop-L") {
        margin: 0;
        margin-right: 24px;
    }
}

.offer,
.legal {
    margin-bottom: 0;

    font-size: 13px;
    line-height: 20px;

    @include respond-to("laptop-L") {
        font-size: 17px;
        line-height: 24px;
    }
}

.offer {
    padding-bottom: 24px;
    border-bottom: 1px solid rgba(66, 78, 88, 0.1);
    margin-bottom: 24px;

    @include respond-to("laptop-L") {
        border-bottom: none;
        margin-bottom: 0;
        padding-bottom: 0;
    }
}

.legal {
    @include respond-to("laptop-L") {
        padding-top: 24px;
    }
}

.column-poster-img {
    object-fit: cover;
    width: 100%;
    height: 280px;
    display: block;
    margin-bottom: 24px;

    @include respond-to("laptop") {
        height: 508px;
        margin-bottom: 0;
    }

    @include respond-to("laptop-L") {
        height: 708px;
    }
}

.title {
    font-size: 36px;
    line-height: 40px;
    margin-bottom: 0;

    @include respond-to("laptop-L") {
        font-size: 56px;
        line-height: 64px;
    }
}

.row {
    @include respond-to("laptop") {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.column-poster {
    @include respond-to("laptop") {
        width: 50%;
    }
}

.description {
    @include respond-to("laptop") {
        display: none;
    }
}
