@import "style/mixins.module.scss";

.table {
    text-align: left;
    width: 100%;

    border-collapse: separate;
    border-spacing: 0;
}

.title {
    margin-bottom: 32px;
}

.tr_thead {
    vertical-align: bottom;
    box-sizing: border-box;
}

.th_thead {
    max-width: 200px;

    border-bottom: 1px solid rgba(66, 78, 88, 0.1);
    text-align: left;

    &:first-child {
        border: none;
    }
}

.tbody,
.tr_tbody {
    box-sizing: border-box;
}

.th_thead,
.td_tbody {
    position: relative;
    padding: 16px;

    font-weight: 400;
    font-size: 17px;
    line-height: 28px;
    color: get_color("gray", "-4");

    overflow-wrap: break-word;
}

.td_tbody {
    min-width: 150px;
    max-width: 218px;

    border-bottom: 1px solid rgba(66, 78, 88, 0.1);
}

.tr_tbody {
    & .td_tbody:first-child {
        padding-left: 0;
        border: none;
    }

    &:last-child {
        .td_tbody {
            border-bottom: none;
            border-bottom-left-radius: 8px;
            border-bottom-right-radius: 8px;
        }
    }
}

.tr_mobile {
    display: none;
}

.td_marked {
    color: get_color("primary", "red");
}

.th_marked {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}

.td_marked {
    &:last-child {
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
    }
}

.th_marked {
    padding: 0;
}

.marked-title {
    margin-bottom: -1px;
    padding: 20px 16px 16px 16px;

    border-top-left-radius: 8px;
    border-top-right-radius: 8px;

    color: get_color("primary", "red");
}

.circle-icon {
    circle {
        fill: transparent;
    }
}

@media (max-width: 768px) {
    .title {
        margin-bottom: 10px;
    }

    .root {
        svg path {
            stroke: currentColor;
        }

        svg circle {
            fill: get_color("gray", "-2");
        }
    }

    .tr_tbody:not(.tr_mobile) {
        & .td_tbody:first-child {
            display: none;
        }
    }

    .tr_thead {
        & .th_thead:first-child {
            display: none;
        }
    }

    .tr_mobile {
        display: table-row;

        & .td_tbody {
            padding: 8px 0 0 0;
            border-bottom: 0;
            text-align: left;
        }
    }

    .th_marked,
    .td_marked {
        background-color: transparent;
        color: get_color("primary", "red");

        & svg path {
            stroke: get_color("primary", "red");
        }
    }

    .th_thead,
    .td_tbody {
        padding: 15px 8px 15px 0;

        font-size: 13px;
        line-height: 20px;
    }

    .th_thead {
        vertical-align: bottom;
    }

    .td_tbody {
        min-width: auto;
        width: 25%;
        text-align: center;
        vertical-align: top;
    }

    .marked-title {
        padding: 0;

        border: none;
        font-weight: 700;
        background-color: transparent;
        color: get_color("primary", "red");
    }
}
